
@media screen{
#node-header .node-filter li{margin:auto 15px;padding:3px
}
#node-header .node-filter li a{text-decoration:none;color:#606060 !important;font-weight:400
}
}
@media (min-width: 768px){
.modal .modal-dialog{width:768px
}
}
@media (max-width: 768px){
#node-header .node-filter li{margin:auto 5px;padding:2px
}
#node-header .node-filter li a{padding-left:2px;padding-right:0;text-decoration:none;color:#606060 !important;font-weight:400
}
}
@media (max-width: 375px){
#node-header .node-filter li{margin:auto 5px;padding:0
}
#node-header .node-filter li a{font-size:12px
}
}
#node-header{background:#f9f9f9;margin:-14px 0 15px;height:50px
}
#node-header .node-filter li{line-height:20px
}
#node-header .node-filter li a:hover{color:#000 !important;padding-bottom:5px;background-color:transparent
}
#node-header .node-filter li:active{color:#000;border-bottom:2px dotted #666666
}
#node-header .node-filter li:first-child:active{border:none
}
#node-header .node-filter .active{color:#000;border-bottom:2px dotted #666666
}
#node-header .node-filter .active a{color:#000 !important;padding-bottom:5px;background-color:transparent
}
#node-header .node-filter i{margin-right:5px
}
#node-header .node-filter .node-button{margin:6px 0;text-align:center
}
#node-header .node-filter .node-button span{padding-left:5px
}
.node-panel{background-color:#f9f9f9;margin-top:-15px;margin-bottom:15px;box-shadow:0 1px 0px rgba(0,0,0,0.02)
}
.node-panel .node-title{font-size:24px;color:#000;margin-bottom:8px
}
.node-panel .node-summry a{color:#000
}
.node-panel .node-summry a:hover{text-decoration:underline
}
.node-panel .node-summry strong{font-weight:bold;color:#000
}
.node-panel .node-summry p:first-child{padding-bottom:5px
}
.node-panel .node-summry p:last-child{padding-top:5px
}



