
.topic{min-height:68px;border-bottom:1px solid #F0F0F0;padding:10px 15px;margin:0 -15px;vertical-align:top;overflow:hidden;font-size:15px
}
.topic a{text-decoration:none !important;outline:none !important;color:#000
}
.topic .topic-avatar{float:left;padding-right:10px
}
.topic .topic-infos{display:table-cell;vertical-align:top;overflow:hidden;width:10000px
}
.topic .topic-infos .topic-title{overflow:hidden
}
.topic .topic-infos .topic-title .topic-node{margin-right:3px;color:#777;font-weight:400;line-height:30px
}
.topic .topic-infos .topic-title a:hover{color:#777 !important
}
.topic .topic-infos .topic-info{color:#ABA8A6;font-size:12px;margin-top:0
}
.topic .topic-infos .topic-info a{color:#ABA8A6 !important
}
.topic .topic-infos .topic-info a:hover{text-decoration:underline !important
}
.topic .topic-replies-count{display:table-cell;text-align:right;padding:15px;padding-right:0;width:100px
}
.topic .topic-replies-count .span-replies{background-color:#CFD3E6;color:#fff;min-width:40px;border-radius:80px;padding:3px 8px 3px 8px;text-align:center
}



