
.node-list .node{margin-bottom:10px;margin-top:0px
}
.node-list .node .media-left{min-width:130px;display:table-cell;vertical-align:top;padding-right:10px
}
.node-list .node label{font-weight:normal;color:#aaa;text-align:right
}
.node-list .node .name{margin-bottom:10px;width:100px;display:block;float:left;text-align:left
}
.node-list .node .name a{color:#333;text-decoration:none
}
.node-list .node .name a:hover{text-decoration:underline
}
.node-list .media .media-body{overflow:hidden;zoom:1;display:table-cell;vertical-align:top;width:10000px;overflow:hidden;zoom:1
}



