
#header{background-color:#fff
}
#header li a{color:#333;padding-bottom:12px
}
#header li a:hover{color:#EB5424;padding-bottom:12px
}
#header .active a{color:#EB5424;padding-bottom:12px
}
#header #nav-menu li a:hover{color:#EB5424 !important;background-color:transparent !important;border-bottom:3px solid #EB5424;padding-bottom:12px
}
#header #nav-menu .active a{color:#EB5424 !important;background-color:transparent !important;border-bottom:3px solid #EB5424 !important;padding-bottom:12px
}
#header .header-brand{border-bottom:3px solid #fff;margin:0;text-align:center;color:#666;line-height:100%;padding:15px;font-size:18px;text-decoration:none;font-family:Helvetica Neue,Helvetica,Arial,sans-serif
}
#header .header-brand b{font-weight:700;color:#eb5424
}
@media screen{
#header .header-search{display:block
}
}
@media (max-width: 992px){
#header .header-search{display:none
}
}
#header .header-search input,#header .header-search input:hover,#header .header-search input:focus{background-color:transparent;border:none;box-shadow:none
}
#header .header-search a{margin-right:5px;color:#000;vertical-align:middle
}
#header .header-search a:hover{color:#000;vertical-align:middle
}



