
@media screen{
.sider{min-height:500px;margin:-15px -30px 0;background-color:transparent;border:none
}
}
@media (max-width: 768px){
.sider{min-height:500px;margin:-15px -15px 0;background-color:transparent;border:none
}
}
.sider .panel{background-color:#fff;margin-bottom:15px;border-radius:3px;border:1px;border-color:#e5e6e9 #dfe0e4 #d0d1d5
}
.sider .panel-title{color:#777;background-color:#fafafa;padding:6px 15px;border:1px solid #eee
}
.sider .panel-body{padding:15px;word-break:break-all
}
.sider .panel-body button{text-align:center;width:100%;height:35px;font-size:14px;font-weight:400;padding:5px 8px
}
.sider .sider-ul{list-style:none;margin:-15px 0
}
.sider .sider-ul li{border-bottom:1px solid #eee;margin:0 -15px 0 -55px;padding:10px 15px
}
.sider .sider-ul li a{color:#000
}
.sider .sider-ul li a:hover{text-decoration:underline
}



